import { Component, Input, Output, OnInit, OnDestroy } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { Store } from '@ngrx/store';
import * as fromRoot from '../../../../app-state/reducers';
import * as startserviceTypes from '../../../../app-state/actions/startservice';
import { SharedService } from '../../../../_services';
import { Subscription } from 'rxjs/Subscription';

@Component({
    templateUrl: './selproperty.component.html',
    styleUrls: ['./selproperty.component.css']
})
export class SelectPropertyComponent implements OnInit, OnDestroy {

    addrList$: Observable<any>;
    nameList: any[];

    esiidListSub: Subscription;
    nonCAARealBPSub: Subscription;

    constructor(private router: Router, private store: Store<fromRoot.State>, private sharedService: SharedService) {
        this.addrList$ = this.store.select(fromRoot.listPorpertyAddress);
    }

    onFormSubmit(inData: any){
        this.store.dispatch(new startserviceTypes.SaveSelectedProperty(inData.selectedProperty))
        this.router.navigate(['/protected/customer/service/stop/selectunits.htm']);
    }

    ngOnInit(){
      this.esiidListSub = this.store.select(fromRoot.getESIIDList).subscribe(result => {
          if(undefined == result.esiidList || result.esiidList.length == 0){
            this.nonCAARealBPSub = this.store.select(fromRoot.getNONCAARealBPList).subscribe(bpNumberList => {
               this.store.dispatch(new startserviceTypes.ListPropertyAddresses({bpNumberList}));
            })
          }else{
            this.store.dispatch(new startserviceTypes.ListPropertyAddresses({esiidList:result.esiidList}));
          }
      })

      this.sharedService.setSelectedUnitsEmpty();

      this.nameList = [
        {name:"Business Information", crumbStatus:"completed",barStatus:"active"},
        {name:"Service Address", crumbStatus:"current",barStatus:"active"},
        {name:"Units", crumbStatus:"",barStatus:""},
        {name:"Submit Request", crumbStatus:"",barStatus:""},
        {name:"Confirmation", crumbStatus:"",barStatus:"false"}
      ]
    }

    ngOnDestroy(){
        this.esiidListSub.unsubscribe();
        this.nonCAARealBPSub.unsubscribe();
    }
}



