import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { HttpModule } from '@angular/http';
//import { EffectsModule } from "@ngrx/effects";
import { SelectPropertyComponent, SelectUnitsComponent, VerifyComponent, ConfirmComponent,
  BusinessInfoComponent, SearchComponent, UnitTableComponent, SSAddrInfoComponent } from './index';
import { MFStopServiceRoutingModule } from './stopservice-routing.module'
import { SharedModule } from '../../../shared/shared.module';
import { MFServiceSahredModule } from '../shared/shared.module';
import { ConfirmStopResolve } from '../../../_resolvers';

@NgModule({
    imports: [
        CommonModule,
        HttpModule,
        FormsModule,
        ReactiveFormsModule,
        MFStopServiceRoutingModule,
        SharedModule,
        MFServiceSahredModule,
       ],
    declarations: [
        SelectPropertyComponent,
        SelectUnitsComponent,
        VerifyComponent,
        ConfirmComponent,
        BusinessInfoComponent,
        SearchComponent,
        UnitTableComponent,
        SSAddrInfoComponent
      ],
    exports: [ SelectPropertyComponent, SelectUnitsComponent, VerifyComponent, ConfirmComponent, BusinessInfoComponent ],
    providers:[ ConfirmStopResolve ]
})
export class MFStopServiceModule { }



