import { Component, Input, Output, OnInit, ViewChild, ElementRef, OnDestroy } from '@angular/core';
import { Store } from '@ngrx/store';
import { Router, ActivatedRoute } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import { Subject } from 'rxjs/subject'

@Component({
    templateUrl: './confirm.component.html',
    styleUrls: ['./confirm.component.css']
})
export class ConfirmComponent implements OnInit, OnDestroy {

    @ViewChild('downloadpdf') pdfElement: ElementRef;
    pdfElementRef: any;

    confirmation$: Observable<any>;
    public serviceType: string = "Stop";
    nameList: any[];

    destroy$: Subject<boolean> = new Subject<boolean>();

    constructor(private router: Router, private route: ActivatedRoute) {

      this.route.data.takeUntil(this.destroy$).subscribe(routeData => {
           this.confirmation$ = new Observable((observer) => {
              observer.next(routeData.data);
              observer.complete();
           })
      })
    }

    ngOnInit(){

      this.nameList = [
        {name:"Business Information", crumbStatus:"completed",barStatus:"active"},
        {name:"Service Address", crumbStatus:"completed",barStatus:"active"},
        {name:"Units", crumbStatus:"completed",barStatus:"active"},
        {name:"Submit Request", crumbStatus:"completed",barStatus:"active"},
        {name:"Confirmation", crumbStatus:"current",barStatus:"false"}
      ]

      this.pdfElementRef = this.pdfElement.nativeElement;
    }

    ngOnDestroy(){
        this.destroy$.next(true);
        this.destroy$.unsubscribe();
    }
}



