import { Component, Input, Output, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import * as fromRoot from '../../../../app-state/reducers';
import * as startserviceTypes from '../../../../app-state/actions/startservice';

@Component({
    templateUrl: './verify.component.html',
    styleUrls: ['./verify.component.css']
})
export class VerifyComponent implements OnInit {

    public verifyInfo$: Observable<any>;
    public info: any;
    public serviceType: string = "Stop";
    isAuthorizeNotClicked: boolean = true;
    public showAuthError: boolean = false;
    nameList: any[];

    constructor(private router: Router, private store: Store<fromRoot.State>) {

      this.verifyInfo$ = Observable.combineLatest(
              store.select(fromRoot.getCustomerDetails),
              store.select(fromRoot.getLoggedInUser),
              store.select(fromRoot.getSelectedUnits),
              store.select(fromRoot.getSelectedSrvcAddrDetails),
              store.select(fromRoot.getSelectedpropertyName),
              store.select(fromRoot.getRelationshipIdForStartStopTxn),
              store.select(fromRoot.getRelationshipId),
              store.select(fromRoot.getStrNodeGuidForStartStopTxn),
            (custDtls, loggedInUser,selectedUnits,selectedSrvcAddress,selectedpropName,relationshipId,selectedPropRelationshipId,strNodeGuidForStartStopTxn) => {
                var info = {businessInfo:{firstName:loggedInUser.firstName,
                            lastName:loggedInUser.lastName,
                            email:loggedInUser.email,
                            phoneNumber: loggedInUser.phoneNum,
                            extn: loggedInUser.phoneNumExtn,
                            faxNumber: loggedInUser.faxNum,
                            addr: custDtls.contactAddress,
                            fedTaxId:(undefined != custDtls.fedTaxId)?custDtls.fedTaxId:'Not Provided',
                            languagePref:custDtls.correspondenceLang
                            },
                            selectedUnits: selectedUnits.unitList,
                            serviceAddress: selectedSrvcAddress,
                            selectedpropName:selectedpropName,
                            relationshipId:relationshipId,
                            selectedPropRelationshipId:selectedPropRelationshipId,
                            strNodeGuidForStartStopTxn:strNodeGuidForStartStopTxn
                          }
                this.info = info;
                return info;
            });
      }

      isAuthorized(isAuthorized){
         this.isAuthorizeNotClicked = !isAuthorized;
         this.showAuthError = this.isAuthorizeNotClicked;
      }

  onSubmitRequest(){
    if(this.isAuthorizeNotClicked){
        this.showAuthError = true;
    }else{
        this.showAuthError = false;
        this.store.dispatch(new startserviceTypes.SubmitStopService(this.info))
        this.router.navigate(['/protected/customer/service/stop/confirm.htm']);
    }
  }

  onBack(){
      this.router.navigate(['/protected/customer/service/stop/selectunits.htm']);
  }

  ngOnInit(){

    this.nameList = [
      {name:"Business Information", crumbStatus:"completed",barStatus:"active"},
      {name:"Service Address", crumbStatus:"completed",barStatus:"active"},
      {name:"Units", crumbStatus:"completed",barStatus:"active"},
      {name:"Submit Request", crumbStatus:"current",barStatus:"active"},
      {name:"Confirmation", crumbStatus:"",barStatus:"false"}
    ]
  }
}



