import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { SelectPropertyComponent, SelectUnitsComponent, VerifyComponent, ConfirmComponent, BusinessInfoComponent } from './index';
import { ConfirmStopResolve } from '../../../_resolvers';

const routes: Routes = [
  { path: 'businessinfo.htm', component: BusinessInfoComponent },
  { path: 'selectproperty.htm', component: SelectPropertyComponent },
  { path: 'selectunits.htm', component: SelectUnitsComponent },
  { path: 'verify.htm', component: VerifyComponent },
  { path: 'confirm.htm',
    component: ConfirmComponent,
    resolve: {
        data: ConfirmStopResolve
    }}
]

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})

export class MFStopServiceRoutingModule { }



