import { Component, Input, Output, OnInit, EventEmitter } from '@angular/core';
import { Router } from '@angular/router';
import { UserService } from '../../../../_services/user.service'
import { Store } from '@ngrx/store';
import { Observable } from 'rxjs/Observable';
import * as fromRoot from '../../../../app-state/reducers';
import * as startserviceTypes from '../../../../app-state/actions/startservice';

@Component({
    selector: 'unit-search',
    templateUrl: './search.component.html',

})
export class SearchComponent implements OnInit {

    constructor() {}

    searchString: string;
    searchText: string;
    showToolTip: boolean;
    leftPos:number;
    topPos:number;
    @Output() onSearchClick = new EventEmitter<any>();

    onSearch(){
        this.onSearchClick.emit({searchString:this.searchString,searchText:this.searchText})
        if(this.searchText == 'Search'){this.searchText = 'Show All'}
        else if(this.searchText == 'Show All'){this.searchText = 'Search'}
    }

    onHelpIcon(currentEvent){
        this.showToolTip = !this.showToolTip;
        this.leftPos=currentEvent.target.offsetLeft+currentEvent.target.clientWidth+14; //14 is width of the arrow - VK
        this.topPos=currentEvent.target.offsetTop-10; //10 is the arrow mark positioned from top in tooltip  -Vk
    }
    getPos(pos)
    {   
            if(pos=="left")
             return this.leftPos;
            if(pos=="top")
                return this.topPos;
    }
    ngOnInit(){
        this.searchText = 'Search';
    }
}



