import { Component, Input, Output,OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { Observable } from 'rxjs/Observable';
import 'rxjs/add/observable/combineLatest';
import { UserService } from '../../../../_services/user.service'
import { Store } from '@ngrx/store';
import * as fromRoot from '../../../../app-state/reducers';

@Component({
    templateUrl: './info.component.html',
    styleUrls: ['./info.component.css']
})

export class BusinessInfoComponent implements OnInit {

    public businessInfo$:Observable<any>;
    nameList: any[];

    constructor(private userService: UserService, private router: Router, private store: Store<fromRoot.State>) {

          this.businessInfo$ = Observable.combineLatest(
                  store.select(fromRoot.getCustomerDetails),
                  store.select(fromRoot.getLoggedInUser),
                (custDtls, loggedInUser) => {
                    return{
                        firstName:loggedInUser.firstName,
                        lastName:loggedInUser.lastName,
                        email:loggedInUser.email,
                        phoneNumber: (undefined != loggedInUser.phoneNum)?loggedInUser.phoneNum:'Not Provided',
                        faxNumber:(undefined != loggedInUser.faxNum)?loggedInUser.faxNum:'Not Provided',
                        addr: custDtls.contactAddress,
                        fedTaxId:(undefined != custDtls.fedTaxId)?custDtls.fedTaxId:'Not Provided',
                        languagePref:custDtls.correspondenceLang
                    }
                });

    }

    onContinue(){
        this.router.navigate(['/protected/customer/service/stop/selectproperty.htm']);
    }

    ngOnInit(){

      this.nameList = [
        {name:"Business Information", crumbStatus:"current",barStatus:"active"},
        {name:"Service Address", crumbStatus:"",barStatus:""},
        {name:"Units", crumbStatus:"",barStatus:""},
        {name:"Submit Request", crumbStatus:"",barStatus:""},
        {name:"Confirmation", crumbStatus:"",barStatus:"false"}
      ]
    }
}



